<template>
  <div class="workbench-container">
    加载中...
  </div>
</template>
<script>
import { mapState, mapMutations } from 'vuex';
import _ from 'underscore';
import PageRefreshService from '@src/services/page-refresh-service';

export default {
  data() {
    return {
      isLoading: false,
    };
  },
  computed: {
    ...mapState({
      clientWidth: state => state.app.global.clientWidth,
      lastTime: state => state.app.lastUpdateTime.workbench || 0,
      loadingStatus: state => state.app.loadingStatus,
      topModules: state => state.page.workbench.topModules,
      modules: state => state.page.workbench.modules,
    }),
  },
  activated() {
    if (+new Date() - this.lastTime > 600000) {
      this.init();
    } else {
      if (_.isEmpty(this.modules)) {
        this.getConfig();
      }
    }
  },

  methods: {
    ...mapMutations('app', ['LAST_UPDATE_TIME']),
    init() {
      this.isLoading = true;
      this.getConfig();
    },

    getConfig() {
      return PageRefreshService.workbench()
        .then(() => {
          this.LAST_UPDATE_TIME({ workbench: +new Date() });
          this.isLoading = false;
        })
        .catch(err => {
          this.isLoading = false;
          console.error(err);
        })
        .then(() => {
          // 刷新选择项目插件列表
          console.log('刷新选择项目插件列表');
          this.hasProjectSelect &&
            this.$refs['project-select-component'].init();
        })
        .then(() => {
          // 触发质检同步数据
          if (
            window.__module_entry_zjapp__ &&
            window.__module_entry_zjapp__.doDownloadPorject &&
            this.loadingStatus &&
            !this.loadingStatus.zjapp
          ) {
            window.__module_entry_zjapp__.doDownloadPorject().catch(err => {
              console.log('同步质检项目数据失败！', err);
            });
          }
        });
    },
  },
};
</script>
<style lang="scss">
.workbench-container {
  // display: flex;
  // justify-content: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
</style>
